<template>
    <!-- banner层 -->
    <div class="banner clearfix">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item,index) in bannerDeta" :key="index">
          <img class="auto-img" v-lazy="item.image_src" alt />
        </van-swipe-item>
      </van-swipe>
    </div>
</template>

<script>
export default {
  data() {
    return {
      //banner图
      bannerDeta: [],
    };
  },
  created() {
    //获取banner数据
    this.Banner();
  },

  methods: {
    //获取banner数据
    Banner() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        methods: "GET",
        url: "/home/swiperdata",
      })
        .then((result) => {
          this.$toast.clear();
          if ((result.data.meta.status = 200)) {
            this.bannerDeta = result.data.message;
            // Replace
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>

<style lang="less" scoped>
  // banner层
  .banner {
    width: 95%;
    height: 170px;
    margin-left: 2.5%;
    margin-top: -3px;
    //轮播图
    .my-swipe .van-swipe-item {
      color: #fff;
      height: 170px;
      font-size: 20px;
      line-height: 150px;
      text-align: center;
      img {
        border-radius: 15px;
      }
    }
  }
</style>