<template>
  <div class="home">
    <!-- 头部 -->
    <div class="header">
      <div class="header_top">
        <div class="header_top_title fl" @click="goPage('Category')">
          <van-icon name="qr" size="20px" />
          <span>分类</span>
        </div>
        <div class="header_gif fl">
          <img
            class="auto-img"
            src="https://image2.suning.cn/uimg/cms/img/159645596551816272.gif"
            alt
          />
        </div>
        <div class="header_top_title fr">
          <van-icon name="manager-o" size="20px" @click="goPage('Account')" />
          <span>用户</span>
        </div>
      </div>

      <div class="header_bottom">
        <van-search
          left-icon
          right-icon="search"
          shape="round"
          @focus="goPage('Search')"
          placeholder="请搜索商品"
          background="rgb(50,150,248)"
        />
