      <div class="header_img">
        <img src="../../assets/images/header.png" alt />
      </div>
    </div>
    <!-- banner层 -->
    <Banner />
    <!-- 导航 -->
    <div class="navigation clearfix">
      <ul>
        <li v-for="(item,index) in navigation" :key="index">
          <img :src="item.img" />
          <div>{{item.name}}</div>
        </li>
      </ul>
    </div>
    <!-- 通知栏 -->
    <div class="noticeBar">
      <van-notice-bar background="rgb(255,254,250)" color="rgb(113,113,113)" :scrollable="false">
        <template #left-icon>
          <img src="../../assets/images/tzl.png" />
        </template>
        <van-swipe vertical class="notice-swipe" :autoplay="3000" :show-indicators="false">
          <van-swipe-item>吉林省物流通知</van-swipe-item>
          <van-swipe-item>2020年7月北京物流通知</van-swipe-item>
          <van-swipe-item>2020年7月新疆物流通知</van-swipe-item>
        </van-swipe>
      </van-notice-bar>
    </div>
    <!-- 天天秒杀 -->
    <Spike />
    <!-- 楼层 -->
    <div class="floordata" v-for="(item,index) in floordata" :key="index">
      <div class="floordata_title">
        <img :src="item.floor_title.image_src" alt />
      </div>
      <div class="product_list clearfix">
        <div class="product_list_left fl">
          <img :src="item.product_list[0].image_src" alt srcset />
        </div>
        <div class="product_list_right fl">
          <img class="auto-img fl" :src="item.product_list[1].image_src" alt srcset />
          <img class="auto-img fl" :src="item.product_list[2].image_src" alt srcset />
          <img class="auto-img fl" :src="item.product_list[3].image_src" alt srcset />
          <img class="auto-img fl" :src="item.product_list[4].image_src" alt srcset />
        </div>
      </div>
    </div>

    <Recommend />
  </div>
</template>

<script>
import Recommend from "../../components/Recommend";
import Banner from "../../components/Banner";
import Spike from "../../components/Spike";

export default {
  data() {
    return {
      // 导航
      navigation: [
        {
          img:
            "https://image2.suning.cn/uimg/cms/img/158140198536389245.png?from=mobile",
          name: "天天低价",
        },
        {
          img:
            "https://image1.suning.cn/uimg/cms/img/158225094251284751.png?from=mobile",
          name: "小鹅超市",
        },
        {
          img:
            "https://image2.suning.cn/uimg/cms/img/158225098618947382.png?from=mobile",
          name: "小鹅拼购",
        },
        {
          img:
            "https://image1.suning.cn/uimg/cms/img/158225102941495158.png?from=mobile",
          name: "5G手机",
        },
        {
          img:
            "https://image1.suning.cn/uimg/cms/img/158225106790473382.png?from=mobile",
          name: "小鹅家电",
        },
        {
          img:
            "https://image1.suning.cn/uimg/cms/img/158225110605546246.png?from=mobile",
          name: "免费水果",
        },
        {
          img:
            "https://image3.suning.cn/uimg/cms/img/158225119197911173.png?from=mobile",
          name: "赚钱消消乐",
        },
        {
          img:
            "https://image3.suning.cn/uimg/cms/img/158225123358512204.png?from=mobile",
          name: "签到有礼",
        },
        {
          img:
            "https://image1.suning.cn/uimg/cms/img/158225129991255185.png?from=mobile",
          name: "领劵中心",
        },
        {
          img:
            "https://image2.suning.cn/uimg/cms/img/158225135374622745.png?from=mobile",
          name: "更多频道",
        },
      ],
      // 楼层
      floordata: [],
    };
  },
  created() {
    //获取导航数据
    // this.Navigation();

    // 获取楼层数据
    this.Floordata();
  },
  methods: {
    //获取导航数据
    Navigation() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        methods: "GET",
        url: "/home/catitems",
      })
        .then((result) => {
          this.$toast.clear();
          if ((result.data.meta.status = 200)) {
            this.navigation = result.data.message;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 获取楼层数据
    Floordata() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        methods: "GET",
        url: "/home/floordata",
      })
        .then((result) => {
          this.$toast.clear();
          if ((result.data.meta.status = 200)) {
            this.floordata = result.data.message;
            // Replace
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //跳转页面
    goPage(name) {
      if (localStorage.getItem("__Tk")) {
        this.$router.push({ name });
      } else {
        this.$toast("请先登录");
      }
    },
  },
  components: {
    Recommend,
    Banner,
    Spike,
  },
};
</script>

